export default {
	props:['todolist'],
	data() {
		return {
			subjects: ['数学', '英语', '政治', '专业课'],
			learnStyle: [
				['做题', '看书', '看视频'],
				['做题', '背单词', '看书', '看视频'],
				['做题', '看书', '看视频'],
				['做题', '看书', '看视频']
			],
			modalName:null,
			listTouchStart: 0,
			listTouchDirection: null,
		}
	},
	methods: {
		complete(todoid,index){
			this.todolist[index].isCompleted = 1
			this.$emit("completeTodoItem",todoid)
		},
		uncomplete(todoid,index){
			this.todolist[index].isCompleted = 0
			this.$emit("uncompleteTodoItem",todoid)
		},
		deleteItem(todoid,index){
			this.todolist.splice(index,1)
			this.$emit("deleteTodoItem",todoid)
		},
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
		// ListTouch计算方向，
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		},
	}
}
